.profile-section {
  min-height: 100vh;
  background: linear-gradient(to top, var(--primary-background-color-gradient), var(--secondary-background-color-gradient)), url("two.1eea5d45.jpeg");
  background-position: top;
  background-repeat: none;
  background-size: cover;
  justify-content: space-around;
  padding: 4rem 1rem;
  display: flex;
}

header {
  background-color: var(--primary-background-color);
}

.user-info {
  width: 50%;
  flex-direction: column;
  align-items: center;
  margin: 10% auto;
  display: flex;
}

.greeting {
  text-align: center;
  width: 100%;
  color: var(--primary-background-color);
  padding: 1rem 2rem;
  font-size: 3.6rem;
}

.profile-pic-wrapper {
  height: 30rem;
  width: 30rem;
  border-radius: 5px solid #fff;
  background-color: #bbb;
  background-size: 100% 100%;
  border-radius: 50%;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  transform: scale(.8);
}

#profile-display-image {
  height: 100%;
  width: 100%;
}

.profile-form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.profile-pic-div {
  height: 20rem;
  width: 20rem;
  -ms-grid-row-align: center;
  background-color: #bbb;
  border: 1px solid gray;
  border-radius: 50%;
  align-self: center;
  position: relative;
  overflow: hidden;
}

#photo {
  height: 100%;
  width: 100%;
}

#file {
  display: none;
}

#uploadBtn {
  height: 4rem;
  width: 100%;
  text-align: center;
  color: #bbb;
  cursor: pointer;
  background: #000000b3;
  font-size: 1.8rem;
  line-height: 30px;
  display: none;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.profile-form fieldset {
  width: 70%;
  height: auto;
  border: none;
  flex-direction: column;
  gap: 1rem;
  margin-top: 4rem;
  font-family: inherit;
  display: flex;
}

legend {
  text-align: center;
  color: #fff;
  margin-bottom: 4rem;
  font-size: 2.4rem;
}

form input {
  height: auto;
  text-transform: lowercase;
  border: none;
  border-radius: 1rem;
  font-size: 1.6rem;
}

form .name, .btn {
  text-transform: capitalize;
}

form input::-webkit-input-placeholder {
  font-size: 1.4rem;
}

form input::-moz-placeholder {
  font-size: 1.4rem;
}

form input:-ms-input-placeholder {
  font-size: 1.4rem;
}

form input::-moz-placeholder {
  font-size: 1.4rem;
}

form input:-ms-input-placeholder {
  font-size: 1.4rem;
}

form input::placeholder {
  font-size: 1.4rem;
}

input:focus, textarea:focus {
  outline: none;
}

.hide-form {
  opacity: 0;
  display: none;
}

.profile-content {
  display: -ms-grid;
  -ms-grid-columns: (1fr) [2];
  height: 100%;
  width: 60%;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin: auto;
  padding: 2rem 3rem;
  display: grid;
}

.profile-content div {
  height: 100%;
  width: 100%;
  background-color: var(--secondary-background-color);
  box-shadow: 0 5px 10px #00052;
  border-radius: 5rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 2rem 3rem;
  display: flex;
}

.profile-content .book {
  background-color: var(--primary-background-color);
  color: #fff;
  cursor: pointer;
  width: 10rem;
  -ms-grid-row-align: center;
  border-radius: 5px;
  align-self: center;
  padding: 1rem;
  font-family: inherit;
  font-size: 1.4rem;
  transition: all .3s ease-in-out;
}

.profile-content .book:hover {
  transform: scale(1.1);
}

.profile-content .book:active {
  transform: translateY(2px);
}

.booking-form {
  min-height: 90vh;
  max-width: 40rem;
  -webkit-backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  color: var(--secondary-background-color);
  background-color: #06070780;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 7rem;
  right: 0;
  transform: translateY(-150%);
}

.hidden-form {
  transform: translateY(0);
}

.schedules {
  color: #fff;
  border: 2px solid var(--primary-background-color);
  padding: 0 2rem;
}

.schedules h2, .schedules h3 {
  color: var(--secondary-background-color);
  text-align: center;
  margin: 0 1rem 1rem;
}

.schedules p {
  text-align: center;
  line-height: 3rem;
}

.book-now {
  width: 100%;
  border: none;
  flex-direction: column;
  gap: 2rem;
  margin: 1rem 2rem;
  padding: 2rem;
  display: flex;
}

.book-now input {
  width: 90%;
  border: none;
  border-radius: 5px;
  padding: .6rem;
}

.submit {
  background-color: var(--primary-background-color);
  cursor: pointer;
  color: #fff;
  border: none;
  padding: 3rem 0;
}

.book-now textarea {
  width: 90%;
  padding: .5rem 1rem;
}

.close-icon {
  color: var(--primary-background-color);
  font-size: 4rem;
}

.schedules-icon {
  text-align: right;
}

